.container {
	opacity: 1;
	height: 100%;
	position: relative;
}
.main {
	display: flex;
	flex-direction: row;
	height: inherit;
	
	
}
.background_text{
    color: rgba(0, 0, 0, 0); 
    position: absolute;
    top: 10rem;
    width: 100%;
    text-align: center;
    font-size: 30rem;
    font-family: "ValorantFont", sans-serif;
    -webkit-text-stroke: 1px rgba(163, 158, 144, 0.438);
    line-height: 25rem;
}
.main_left{
	box-sizing: border-box;
	width: 60%;
	border-right: 1px solid rgb(167, 167, 167);
	position: relative;
	overflow-y: auto;
	height: calc(100vh - 210px)
}
.main_left::after{
	content: '';
	position: absolute;
	height: 3rem;
	width: .5rem;
	bottom: 0;
	
	background: rgb(47, 47, 49);
}
.info {
	box-sizing: border-box;
	padding-top: 3rem;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	margin-left: 8rem;
	border-left: 1px solid rgb(145, 145, 145);
	height: 100%;
	padding-bottom: 4rem;
	box-sizing: border-box;
	position: relative;
}
.info::after{
	content: '';
	height: 3rem;
	width: 40%;
	top: 0;
	right: 0;
	background: rgb(5,16,34);
	position: absolute;
	-webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
clip-path: polygon(0 0, 100% 0, 100% 100%, 5% 100%);
}
.info::before{
	content: '';
	height: .5rem;
	width: .5rem;
	top: 0;
	left: 0;
	background: rgb(255,70,85);
	position: absolute;
}
.block{
	position: absolute;
	height: .7rem;
	width: 3rem;
	top: 0;
	left: 0;
	background: rgb(255,70,85);
}
.textbox {
	color: rgb(112, 112, 112);
}
.filter{
	padding-left: 1rem;
	padding-bottom: 2rem;
	border-bottom: 1px solid rgb(145, 145, 145);
}

.title{
	height: fit-content;
	padding-left: 1rem;
	font-size: 3rem;
	margin: 0;
	font-family: 'GoodTimes', 'Roboto', sans-serif;
	
}
.main_right{
	width: 40%;
	height: calc(100vh - 210px);
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	position: relative;
}
.main_right::before{
	content: '';
	position: absolute;
	height: 3rem;
	width: 30%;
	top: 0;
	left: 0;
	background: rgb(5,16,34);
	-webkit-clip-path: polygon(0 0, 100% 0, 95% 100%, 0% 100%);
clip-path: polygon(0 0, 100% 0, 95% 100%, 0% 100%);
}
.info_media {
	position: relative;
	margin-top: 4rem;
}
.info_text_container{
	position: relative;
	width: 90%;
	height: fit-content;

}
.info_text_container::after{
	content: '.  . /';
	font-size: 2rem;
	top: 50%;
	right: -2rem;
	color: rgb(255,70,85);
	position: absolute;
}
.info::before{
	content: '';
	height: 3rem;
	width: .3rem;
	top: 50%;
	left: 0;
	color: rgb(255,70,85);
	position: absolute;
}
.info_text{
	font-size: 1.6rem;
	padding: 2rem;
	margin: 0;
}
.filterbox{
	margin-top: 1rem;
	padding-top: 1rem;
	position: relative;
	width: 100%;
	display: flex;
	flex-direction: row;
	width: fit-content;
	flex-wrap: wrap;
	padding-right: 1rem;
	padding-bottom: 1rem;

}
.filterbox::before{
	box-sizing: border-box;
	content: "";
    display: block;
	position: absolute;
	height: calc(50% - .9rem);
	width: 100%;
	color: black;
    border: 2px solid rgb(246,68,82);
	left: 0;
	top: 0;
    border-bottom: 0;
}
.filterbox::after{
	box-sizing: border-box;
    content: "";
    display: block;
    position: absolute;
    height: calc(50% - .9rem);
    width: 100%;
	border: 2px solid rgb(246,68,82);
	bottom: 0;
    border-top: 0;
}

.difficulty{
	padding: 3px 5px 3px 5px !important;
}
.icon {
	margin-left: 1rem;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
		'Helvetica Neue', sans-serif;
	width: fit-content;
	font-weight: 400;
	letter-spacing: 1px;
	padding: 3px 5px 3px 5px;
	border-radius: 5px;
	color: white;
	background-color: rgb(53, 142, 197);
	font-size: 1.5rem;
}
.attacking {
	background-color: rgb(255, 123, 0);
}
.defending {
	background-color: rgb(99,56,126);
}
.colorGreen{
    background-color: rgb(98, 163, 29);
}
.colorYellow{
    background-color: rgb(163, 157, 94);
}
.colorRed{
    background-color: rgb(175, 41, 41);
}
.images{
    height: fit-content;
    width: 100%;
}

.map_container{
	width: fit-content;
	display: flex;
	justify-content: center;
	align-self: center;
}
.map{
	display: block;
	width: 100%;
	
}
.map_wrapper{
	border: 2px solid rgb(105, 105, 105);
	height:fit-content;
	position: relative;
}
.map_wrapper::after{
    content: "";
    display: block;
    position: absolute;
    height: 6rem;
    width: .5rem;
    top: calc(50% - 3rem);
    right: -.5rem;
    background-color: rgb(246,68,82);
}
.map_wrapper::before{
    content: "";
    display: block;
    position: absolute;
    height: 6rem;
    width: .5rem;
    top: calc(50% - 3rem);
    left: -.5rem;
    background-color: rgb(246,68,82);
}
.map_container{
	height: fit-content;
	width: 80%;
	position: relative;
	
}
.map_container::before{
	content: '';
	position: absolute;
	width: .5rem;
	height: 2rem;
	background: rgb(151, 151, 151);
	top: 0;
	right: 2rem;
}
.map_block{
	position: absolute;
	height: 1.5rem;
	width: 40%;
	background: rgb(139,151,143);
	bottom: -1.5rem;
	right: -1px;
	z-index: 0;
}
.map_line{
	position: absolute;
	height: .4rem;
	width: .4rem;
	background: rgb(139,151,143);
	bottom: 0rem;
	left: -5rem;
	z-index: 0;
}
.videoWrapper{
	box-sizing: border-box;
	margin-top: 1rem;
	position: relative;
	width: 80%;
	height: auto;
}
.right_container{
	display: none;
}
@media only screen and (max-width: 1400px) {
	.main_right{
		display: none;
	}
	.right_container{
		left: -1px;
		display: flex;
		position: relative;
		margin-top: 4rem;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		width: 100%;
		height: 100%;
		border-left: 1px solid rgb(167, 167, 167);
		box-sizing: border-box;
		padding-bottom: 5rem;
	}
	.main_left{
		width: 100%;
	}
	.map_container{
		width: 60%;
	}
	.videoWrapper{
		height: fit-content;
		margin-bottom: 3rem;
	}
   }
  

   @media only screen and (max-width: 1300px){
	   .right_container{
		   
	   }
   }
   @media only screen and (max-width: 1000px){
	.info{
		margin-left: 3rem;
	}
}
@media only screen and (max-width: 1000px){
	.right_container{
		margin-top: 0rem;
	}
}
@media only screen and (max-width: 700px){
	.title{
		font-size: 2rem;
	}
	.info{
		margin-left: 8rem;
	}
}
@media only screen and (max-width: 550px){
	.info{
		margin-left: 3rem;
	}
	.container{
		height: 100%;
	}
	.main_left{
		height: calc(100vh - 95px);
	}
}