.container{
    box-sizing: border-box;
    overflow: visible;
    flex-grow: 1;
    height: calc(100vh - 200px);
    background-image: url("../Media/intro_background.jpg");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
   z-index: 100;
}

.block3{
    content: "";
    display: block;
    position: absolute;
    height: 10rem;
    width: 60rem;
 bottom: -10rem;
    right: 0;
    background-color: rgb(15,25,35);
}
.main{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
   
}
.background_text{
    color: rgba(0, 0, 0, 0); 
    position: absolute;
    top: 0rem;
    font-size: 25rem;
    font-family: "ValorantFont", sans-serif;
    -webkit-text-stroke: 1px rgba(71, 71, 71, 0.589);
    line-height: 25rem;
}
.header{
    position: relative;
    height: 100%;
    width: 20%;
}
.title{
    margin: 0;
    position: absolute;
    top: -4rem;
    right: 0;
    letter-spacing: 3px;
    font-size: 3rem;
    display: flex;
    justify-content: space-around;
    width: calc(100vh - 200px);
    transform-origin: bottom right;
    transform: rotate(-90deg);
    /* transform: rotate(90deg) translate(50%, 100%); */
    text-align: center;
    font-family: 'Tungsten-Bold', sans-serif;
    color: rgb(238, 228, 228); 
    /* -webkit-text-stroke: 1px rgb(83, 83, 83) */
}
.main_wrapper{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    width: 80%;
    float: right;
    border-left: 2px solid rgb(238, 238, 238);
    box-sizing: border-box;
}
.right_line{
    position: absolute;
    height: 100%;
    width: 20%;
    border-left: 2px solid rgb(238, 238, 238);
    z-index: 0;
    top: 0;
    right: 0;
}
.left{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 40%;
    height: 100%;
    border-right: 1px solid rgb(238, 238, 238);
    overflow: hidden;
}
.main_title{
    position: relative;
    margin-top: 20%;
    color:rgb(235, 235, 235);
    font-size: 7rem;
    font-family: "DrukWide";
    border-bottom: 1px solid rgb(238, 238, 238);
    padding-left: 1rem;
    padding-right: 4rem;
    letter-spacing: .5rem;
    margin-bottom: 0;
}

.main_title::after{
    content: "";
    display: block;
    position: absolute;
    height: .5rem;
    width: 1.2rem;
 bottom: 0;
    right: 0;
    background-color: rgb(5,16,34);
}

.right{
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 60%;
    height: 100%;
}
.right::before{
    content: '';
    position: absolute;
    height: 3rem;
    width: .2rem;
    background: rgb(238, 238, 238);
    top: 10%;
    left: 0;
}
.info{
    box-sizing: border-box;
    position: relative;
    height: 100%;
    width: calc(100% - 7rem);
   font-family: "GoodTimes";
   border-left: 1px solid rgb(238, 238, 238);
   margin-left: 7rem;
   color: white;
   /* background: rgba(15, 25, 35, 0.719); */
}
.info_text{
    position: relative;
    border-top: 1px solid rgb(238, 238, 238);
    font-family: "Interstate", sans-serif;
    font-size: 1.6rem;
    font-weight: 200;
    padding: 4rem 2rem 2rem 2rem;
    margin: 0;
    background: rgb(15,25,35);
    line-height:  2.2rem;
}

.origin{
    padding: 1rem;
    font-size: 1.2rem;
    font-weight: 300;
    width: fit-content;
    clip-path: polygon(89% 0, 100% 8%, 100% 83%, 89% 100%, 0 100%, 0 0);

}
.origin::after{
    content: "";
    display: block;
    position: absolute;
    height: .5rem;
    width: 1.2rem;
    background-color: rgb(15,25,35);
}

.sova_image_container{
    height: 100%;
    width: 100%;
    display: block;
    position: relative;
    margin-left: 0;
}
.inner{
    height: 100%;
    width: 100%;
}
.sova_image{
    position: absolute;
    bottom: -10rem;
    width: 100rem;
    
    z-index: 1;
}
.block{
    border-top: 1px solid rgb(238, 238, 238);
    z-index: 10;
    width: 40rem;
    height: 15rem;
    bottom: 0;
    left: 29rem;
    position: absolute;
    background: rgb(15,25,35);
    width: 100%;
}
.block2{
    border-top: 1px solid rgb(238, 238, 238);
    z-index: 0;
    width: 40rem;
    height: 15rem;
    bottom: 0;
    left: 0;
    position: absolute;
    background: rgb(15,25,35);
    width: 100%;
}



.sova_text{
    position: absolute;
    font-size: 20rem;
    right: -20%;
    top: 30rem;
    transform: rotate(90deg);
    font-family: 'DrukWide';
    z-index: -1;
    color: rgba(0, 0, 0, 0); 
    -webkit-text-stroke: 1px rgb(238, 238, 238);
}
.video_player{
    height: 100%;
    background: rgb(15,25,35);
}
.video{
    
    width: fit-content !important;
    padding: 4px;
    
}

@media only screen and (max-height: 1090px) {
    .container{
        min-height: 900px !important;
    }
  }
  @media only screen and (max-width: 1650px){
    .sova_image{
        width: 90rem;
    }
    .header{
        position: relative;
        height: 100%;
        max-width: 5%;
    }
    .right_line{
        width: 5%
    }
    .main_wrapper{
        width: 100%;
    }
  }
  @media only screen and (max-width: 1300px){
      .left{
          width: 55%;
      }
      .right{
          width: 45%;
      }
      .sova_image{
          width: 80rem;
          left: -10rem;
      }
      .block{
          left: 15rem;
      }
  }
  @media only screen and (max-width: 950px){
      .sova_text{
          display: none;
      }
      .main_title{
          font-size: 5rem;
      }
  }
  @media only screen and (max-width: 800px){
    .right{
        display: none;
    }
    .left{
        width: 90%;
    }.container{
        border-bottom: 1px solid white;
    }
  }
  @media only screen and (max-width: 600px){
      .info{
          margin-left: 1rem;
          width: 100%;
      }
      .header{
          display: none;
      }
      .main_title{
          margin-top: 4rem;
      }
      .info_text{
          font-size: 1.4rem;
      }
      .video{
          height: fit-content !important;
      }
      .video_player{
    height: 100%;

}
.container{
    min-height: 81rem !important;
}
  } 